@import url(https://fonts.googleapis.com/css2?family=Kalam&family=Kaushan+Script&family=Permanent+Marker&display=swap);
body{margin:0;font-family:'Kalam', cursive;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}.App{background-color:#00303F;color:#fff}.App .main-title{text-align:center}.App nav{padding:0 1rem;background-color:#003053}.App .content{min-height:calc(100vh - 70px - 64px);padding:1.5rem 0}.App .card-wrap .card-flipper{transition:0.6s;-webkit-transform-style:preserve-3d;transform-style:preserve-3d;position:relative}.App .card-wrap .card-flipper .card{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;cursor:pointer}.App .card-wrap .card-flipper .card.front,.App .card-wrap .card-flipper .card.back{height:250px;background-color:#5BB0CA;border-radius:5%}.App .card-wrap .card-flipper .card.front .favorite-icon,.App .card-wrap .card-flipper .card.back .favorite-icon{width:100%;text-align:right;padding:10px}.App .card-wrap .card-flipper .card.front .favorite-icon .material-icons.active,.App .card-wrap .card-flipper .card.back .favorite-icon .material-icons.active{color:#E4C439}.App .card-wrap .card-flipper .card.front{z-index:2}.App .card-wrap .card-flipper .card.back{-webkit-backface-visibility:hidden;backface-visibility:hidden;position:absolute;top:-7px;left:0;width:100%;-webkit-transform:rotateY(180deg);transform:rotateY(180deg)}.App .card-wrap .card-flipper .card .card-content{-webkit-flex-grow:1;flex-grow:1;text-align:center}.App .card-wrap .card-flipper .card .card-content .card-title{color:#fff;text-transform:capitalize}.App .card-wrap.active .card-flipper{-webkit-transform:rotateY(180deg);transform:rotateY(180deg)}.App .pokemon-list{display:grid;grid-template-columns:repeat(auto-fill, minmax(250px, 1fr));grid-gap:1rem;gap:1rem}.App footer{background-color:#003053}.hide-on-med-and-down{display:block !important}@media only screen and (max-width: 700px){nav .brand-logo{left:10%;font-size:1.5rem}nav ul a{padding:0 10px}}

